@import '../../../assets/css/variables';

.auth-container {
  display: flex !important;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  &-seg {
    padding: 8rem 5rem !important;
    .header {
      margin-bottom: 1.5rem;
    }
    .form-group-container {
      display: flex;
      flex-direction: column;
      .input {
        margin-bottom: 1rem;
      }
    }
    .auth-footer {
      display: flex;
      flex-direction: row;
      justify-content: center;
      p {
        margin-bottom: 0;
        margin-right: 0.2rem;
      }
      a {
        font-weight: 600;
      }
    }
    .auth-error {
      text-align: center;
      color: $invalid;
    }
  }
}
